 <v-layout row>
      <v-flex xs12 sm6 offset-sm3>
        <v-card>

          <v-list>
            <v-list-tile     v-for="person in persons"     :key="person._id">
  
              <v-list-tile-content>
                    <v-list-tile-title    v-text="person.name" ></v-list-tile-title>
              </v-list-tile-content>

              <v-list-tile-content>
                    <v-list-tile-title v-text="person.age"></v-list-tile-title>
              </v-list-tile-content>
  
            </v-list-tile>
          </v-list>

