// eslint-disable-next-line
import axios from 'axios';

  export default {
  data () {
    return {
     persons: [] 
    }
  },

  mounted() {
    axios
      .get("http://localhost/api/persons")
      // .get("http://soliton.eastus.cloudapp.azure.com/api/persons")
      // .get("api/persons")
      .then(response => {
        this.persons = response.data.persons;
      })
      .catch(error => {console.log(error)})
  }
}
</script>

